
'use client';

import type { ReactNode } from 'react';
import { useEffect, useState } from 'react';
import { usePathname } from 'next/navigation';
import { GeistSans } from 'geist/font/sans';
import { GeistMono } from 'geist/font/mono';
import './globals.css';
import { Toaster } from '@/components/ui/toaster';
import UnifiedSidebar from '@/components/layout/UnifiedSidebar';
import { SidebarProvider, SidebarInset, SidebarTrigger } from '@/components/ui/sidebar';
import { ThemeProvider } from '@/components/layout/ThemeProvider';
import { PanelLeft } from 'lucide-react';
import { cn } from '@/lib/utils';
import { Button } from '@/components/ui/button';

const geistSans = GeistSans;
const geistMono = GeistMono;

export default function RootLayout({
  children,
}: Readonly<{
  children: ReactNode;
}>) {
  const pathname = usePathname();
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  const hideSidebarAndMobileHeader = pathname === '/auth/callback' || pathname.startsWith('/admin');

  return (
    <html lang="zh-CN" suppressHydrationWarning>
      <head>
        <title>VRCub建筑中心</title>
        <meta name="description" content="VRCub建筑中心 - 展示精选的Minecraft建筑作品，并为它们投票。" />
        <link rel="icon" href="/favicon.ico" sizes="any" />
      </head>
      <body className={`${geistSans.variable} ${geistMono.variable} antialiased bg-background text-foreground`}>
        <ThemeProvider
          attribute="class"
          defaultTheme="dark"
          enableSystem
          disableTransitionOnChange
        >
          {/* Conditional rendering based on 'mounted' state happens INSIDE ThemeProvider */}
          {mounted ? (
            <SidebarProvider defaultOpen={true}>
              {!hideSidebarAndMobileHeader && <UnifiedSidebar />}
              
              <SidebarInset className={cn(
                "flex-1 flex flex-col bg-background overflow-x-hidden"
              )}>
                {!hideSidebarAndMobileHeader && (
                  <header className="sticky top-0 z-40 h-14 flex items-center gap-4 border-b border-sidebar-border bg-background px-4 md:hidden">
                    <SidebarTrigger variant="outline" size="icon" className="shrink-0">
                      <PanelLeft className="h-5 w-5" />
                      <span className="sr-only">Toggle navigation menu</span>
                    </SidebarTrigger>
                    <h1 className="text-lg font-semibold text-sidebar-foreground">VRCub建筑中心</h1>
                  </header>
                )}
                <div className={cn(
                  "flex-1 overflow-y-auto",
                  !hideSidebarAndMobileHeader && "p-4 sm:p-6 lg:p-8",
                  pathname === '/login' && "flex" 
                )}>
                  {children}
                </div>
              </SidebarInset>
            </SidebarProvider>
          ) : (
            // Render a minimal fallback or nothing until mounted to avoid hydration errors
            // For a full-page app, often the children are rendered directly if they don't cause issues,
            // but here we are specifically trying to ensure ThemeProvider stabilizes first.
            // A simple div wrapper for children might be enough if children are server components.
            <div className={cn(
                "flex-1 overflow-y-auto",
                 hideSidebarAndMobileHeader ? "p-0" : "p-4 sm:p-6 lg:p-8", // Basic padding if sidebar isn't there
                 pathname === '/login' && "flex"
              )}>
              {children}
            </div>
          )}
          <Toaster />
        </ThemeProvider>
      </body>
    </html>
  );
}
