SidebarContent.displayName = "SidebarContent"


const sidebarMenuButtonVariants = cva(
  "group/button peer/menu-button flex w-full items-center gap-3 overflow-hidden rounded-md text-left text-sm outline-none ring-offset-background focus-visible:ring-1 focus-visible:ring-sidebar-ring transition-colors duration-150 text-sidebar-foreground hover:bg-sidebar-accent hover:text-sidebar-accent-foreground active:bg-sidebar-accent/80 data-[active=true]:bg-sidebar-primary data-[active=true]:font-semibold data-[active=true]:text-sidebar-primary-foreground disabled:pointer-events-none disabled:opacity-50 [&_svg]:size-5 [&_svg]:shrink-0",
  {
    variants: {
      variant: {
        default: "",
      },
      isActuallyCollapsed: {
        true: "justify-center p-2.5",
        false: "px-3 py-2.5 justify-start",
      }
    },
    defaultVariants: {
      variant: "default",
      isActuallyCollapsed: false,
    },
  }
)

export interface SidebarMenuButtonProps
  extends React.ButtonHTMLAttributes<HTMLButtonElement>,
    Omit<VariantProps<typeof sidebarMenuButtonVariants>, "isActuallyCollapsed"> {
  asChild?: boolean;
  isActive?: boolean;
  tooltip?: string | React.ReactNode;
}

const SidebarMenuButton = React.forwardRef<HTMLButtonElement, SidebarMenuButtonProps>(
  (
    {
      className,
      variant,
      asChild = false,
      isActive = false,
      tooltip,
      children,
      ...props
    },
    ref
  ) => {
    const Comp = asChild ? Slot : "button";
    const { isMobile, isActuallyCollapsed } = useSidebar();

    const {
      // Filter out props that are specific to this component's logic
      // and not meant to be passed down to the DOM element or Slot child.
      // 'isActive' is used for data-active attribute.
      // 'tooltip' is used by Tooltip component.
      // 'variant' and 'isActuallyCollapsed' are used by cva.
      ...passThroughProps
    } = props;


    const buttonElement = (
      <Comp
        className={cn(sidebarMenuButtonVariants({ variant, isActuallyCollapsed, className }))}
        ref={ref}
        data-sidebar="menu-button"
        data-active={isActive}
        {...passThroughProps}
      >
        {children}
      </Comp>
    );

    if (!tooltip || isMobile || !isActuallyCollapsed) {
      return buttonElement;
    }

    return (
      <Tooltip>
        <TooltipTrigger asChild>{buttonElement}</TooltipTrigger>
        <TooltipContent
          side="right"
          align="center"
          sideOffset={6}
          className="bg-sidebar-accent text-sidebar-accent-foreground border-sidebar-border text-xs px-2 py-1 shadow-md"
        >
          {typeof tooltip === 'string' ? <p>{tooltip}</p> : tooltip}
        </TooltipContent>
      </Tooltip>
    );
  }
);
SidebarMenuButton.displayName = "SidebarMenuButton"


